
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
button {
  height: 60px;
  width: 40px;
  position: absolute;
  left: 10px;
  padding: 0;
  background-color: rgba(255, 255, 255, 0.1);
  border: 0;
}
button i {
  text-decoration: none;
  font-size: 40px;
  color: black;
}

html,
body,
#app {
  height: 100%;
  margin: 0;
  text-decoration: none;
}
