  width: 50px;
  height: 100px;
}
h2 {
  color: coral;
  font-size: 22px;
}
.home {
  background-color: antiquewhite;
  height: 100%;
  padding-top: 10px;
}
a {
  text-decoration: none;
  color: aliceblue;
}
.big {
    margin: 30px 18px;
}
